export default "@import\"https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap\";body{-webkit-font-smoothing:subpixel-antialiased !important;-webkit-backface-visibility:hidden;-moz-backface-visibility:hidden;-ms-backface-visibility:hidden}.weather-widget{font-family:\"Source Sans Pro\",Helvetica,Arial,sans-serif;font-size:10px;color:#2c3e50;position:relative;width:225px;border:1px solid rgba(0,0,0,.1);box-sizing:border-box}.weather-widget__edit-button{position:absolute;right:11px;top:11px;opacity:.7;transition:300ms;cursor:pointer}.weather-widget__edit-button:hover{opacity:1}.weather-widget__view{padding:16px}.weather-widget__view+.weather-widget__view{margin-top:32px}.weather-widget__title{font-weight:bold;font-size:12px}.weather-widget__main-info{display:flex;align-items:center}.weather-widget__icon img{max-width:100px}.weather-widget__temp{font-size:24px;font-weight:bold;white-space:nowrap}.weather-widget__other-info{display:flex;flex-wrap:wrap}.weather-widget__other-info-item{margin-top:16px;margin-right:16px;line-height:12px}.weather-widget__other-info-item_wind{display:flex;align-items:center}.weather-widget__other-info-item_wind img{height:12px;margin-right:4px}.weather-widget__other-info-item_pressure{display:flex;align-items:center}.weather-widget__other-info-item_pressure img{height:12px;margin-right:4px}.weather-widget__edit{position:absolute;width:100%;height:100%;left:0;top:0;background-color:#fff;padding:16px;box-sizing:border-box}.weather-widget__location-item{font-size:12px;padding:8px 4px;background-color:rgba(0,0,0,.1);border-radius:4px;margin-top:4px;position:relative;display:flex;align-items:center;justify-content:space-between}.weather-widget__drag-handle{cursor:-webkit-grab;cursor:grab;margin-left:4px;display:flex;align-items:center}.weather-widget__drag-handle img{height:12px}.weather-widget__remove-city{padding:4px;opacity:.5;transition:300ms;cursor:pointer;display:flex;align-items:center}.weather-widget__remove-city:hover{opacity:1}.weather-widget__remove-city img{width:12px;height:12px}.weather-widget__search{margin-top:16px;position:relative}.weather-widget__search-label{display:block;font-size:12px;font-weight:bold}.weather-widget__search-field{display:block;font-size:12px;line-height:12px;padding:4px;border:1px solid rgba(0,0,0,.1);border-radius:4px;width:100%;box-sizing:border-box;transition:300ms}.weather-widget__search-field:focus{outline:none;border-color:#000}.weather-widget__search-result{background-color:#fff;box-shadow:0 0 2px rgba(31,41,55,.08);border:1px solid rgba(0,0,0,.1);border-radius:4px}.weather-widget__search-result-item{display:flex;align-items:center;justify-content:space-between;font-size:12px;line-height:12px;padding:8px 4px;border-bottom:1px solid rgba(0,0,0,.1);transition:300ms;cursor:pointer}.weather-widget__search-result-item:last-child{border:none}.weather-widget__search-result-item:hover{background-color:rgba(143,143,143,.1)}.weather-widget__clear-search{position:absolute;right:2px;top:17px;padding:4px;opacity:.5;transition:300ms;cursor:pointer}.weather-widget__clear-search:hover{opacity:1}.weather-widget__clear-search img{width:8px;height:8px}.weather-widget__close{position:absolute;right:12px;top:12px;padding:4px;opacity:.5;transition:300ms;cursor:pointer}.weather-widget__close:hover{opacity:1}.weather-widget__add-city{padding:2px;opacity:.5;transition:300ms;cursor:pointer}.weather-widget__add-city:hover{opacity:1}.weather-widget__add-city img{height:10px}.weather-widget__empty-list{padding:48px 24px;text-align:center;font-size:16px}.weather-widget__empty-list button{margin-top:16px;display:block;background-color:rgba(0,0,0,.1);border:1px solid rgba(0,0,0,.5);padding:4px;width:100%;border-radius:4px}.weather-widget__loader{position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(255,255,255,.8);display:flex;align-items:center;justify-content:center;font-size:16px;font-style:italic;font-weight:bold}.weather-widget__spinner{-webkit-animation:spin .1s linear infinite;animation:spin .1s linear infinite}@-webkit-keyframes spin{100%{transform:rotate(360deg)}}@keyframes spin{100%{transform:rotate(360deg)}}[v-cloak]{display:none}"